<template>
  <div style="display: flex;">
    <div class="intro-text" style="position: absolute;min-height: calc(100vh - 60px);height: auto;width: 155px; background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router
               background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
          <el-menu-item index="/taskCreate">
            <i class="el-icon-caret-right"></i>
            <span slot="title">创建</span>
          </el-menu-item>
          <el-menu-item index="/taskView">
            <i class="el-icon-caret-right"></i>
            <span slot="title">查看</span>
          </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="display: flex;position: absolute;margin-left: 250px;margin-top: 60px;">
      <!--  创建任务页面1：填写任务名称、描述以及选择任务类型和周期-->
      <!--  页面二合一：加上点检周期、时间、地点-->

      <div>
        <el-card style="width:100%;height: 560px;margin-left: 2%;margin-top:2%;overflow: auto">
          <div style="width: 1000px;background: #FFFFFF;;border-radius: 2px;;justify-content: center;">
            <div style="margin-bottom: 20px;margin-left: 40px">
              <el-steps :active="active" finish-status="success" align-center style="margin-top: 15px;">
                <el-step title="创建任务" @click.native="re0"></el-step>
                <el-step title="填写信息" @click.native="re1"></el-step>
                <el-step title="勾选设备" @click.native="re2"></el-step>
                <el-step title="勾选人员" @click.native="re3"></el-step>
                <el-step title="确认创建"></el-step>
              </el-steps>
            </div>
            <!--选择任务领取人-->
            <div v-if="active===0">
              <div style="display: flex;justify-content: center;align-items: center;margin-right: 180px;margin-top: 60px">
                <div class="smallbox" style="width: 280px;height: 120px;text-align: center; margin:5% 2% 1% 25%;border-radius: 5%"
                     @click="authority=1,active=1">
                  <img src="../../assets/运行.png" width="246" height="126"/>
                </div>
                <div class="smallbox" style="width: 280px;height: 120px;text-align: center;margin:5% 1% 1% 75px;border-radius: 5%"
                     @click="authority=2,active=1">
                  <img src="../../assets/维修.png" width="246" height="126"/>
                </div>
                <div class="smallbox" style="width: 280px;height: 120px;text-align: center;margin:5% 1% 1% 75px;border-radius: 5%"
                     @click="authority=4,active=1,form.type='临时任务'">
                  <img src="../../assets/安全员-Hover.png" width="246" height="126"/>
                </div>
              </div>
            </div>
            <!--选择任务类型，填写任务信息-->
            <div v-if="active===1">
              <div style="display: flex;justify-content: center;align-items: center;padding-top: 40px">
                <el-form :label-posiyion="left" label-width="140px" :model="form" :rules="rules" ref="formRef">
                  <el-form-item label="任务名称:" prop="packageName">
                    <el-input v-model="form.packageName" placeholder="请输入任务名称"></el-input>
                  </el-form-item>
<!--                  <el-form-item label="任务周期:" prop="任务周期">-->
<!--                    <el-select v-model="form.taskCycle" style="width: 400px">-->
<!--                      <el-option-->
<!--                          v-for="item in 任务周期列表"-->
<!--                          :key="item.value"-->
<!--                          :label="item.label"-->
<!--                          :value="item.value">-->
<!--                      </el-option>-->
<!--                    </el-select>-->
<!--                  </el-form-item>-->
                  <el-form-item label="点检周期:" prop="点检周期" v-if="authority !== 4">
                    <el-input v-model="form.checkCycle" placeholder="请输入点检周期" style="width: 400px"></el-input>
                  </el-form-item>
                  <el-form-item label="超时时间:" prop="超时时间" v-if="authority !== 4">
                    <el-input v-model="form.timeout" placeholder="请输入超时时间" style="width: 400px"></el-input>
                  </el-form-item>
                  <el-form-item label="任务类型:" prop="任务类型" v-if="authority !== 4">
                    <el-select v-model="form.type" style="width: 400px">
                      <el-option
                          v-for="item in 任务类型列表"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="点检时间:" prop="点检时间">
                    <el-date-picker
                        is-range
                        v-model="time"
                        type="datetimerange"
                        style="width: 400px"
                        format="yyyy-MM-dd HH:mm"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间"
                        placeholder="选择时间范围">
                    </el-date-picker>
                  </el-form-item>
                  <el-form-item label="任务描述:" prop="任务描述">
                    <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 3}"
                              v-model="form.description" placeholder="请输入任务描述" style="width: 400px;"></el-input>
                  </el-form-item>

                </el-form>
              </div>
            </div>
            <!--选择设备或仪表-->
            <div v-if="active === 2">
              <!--选择设备-->
              <div v-if="form.type==='设备点检任务'">
                <div style="display: flex; justify-content:center;">
                  <div style="width: 80%;">
                    <div>
                      <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容"
                                v-model="searchText"></el-input>
                      <el-button type="primary" @click="getDevice(1)">查询</el-button>
                      <el-button type="info" @click="reset">重置</el-button>
                      <div style="display: flex;margin-top: 10px">
                        <div style="width: 99%">
                          <el-table :data="equipmentsList" border height="312" align="center" stripe
                                    :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}">
                            <el-table-column prop="equipmentNum" label="设备编号" width="100" align="center"></el-table-column>
                            <el-table-column prop="department" label="部门" width="160" align="center"></el-table-column>
                            <el-table-column prop="equipmentName" label="设备名称" align="center"></el-table-column>
                            <el-table-column prop="type" label="设备类型" width="100" align="center"></el-table-column>
                            <el-table-column label="选择" align="center" width="80">
                              <template v-slot="scope">
                                <el-button size="mini" type="primary" plain @click="equipmentsAdd(scope.row)">选择
                                </el-button>
                              </template>
                            </el-table-column>
                          </el-table>
                          <el-pagination
                              @current-change="handleCurrentChangeDevice"
                              :current-page="equipmentsPageNum"
                              :page-size="equipmentsPageSize"
                              layout="total, prev, pager, next"
                              :total=equipmentsTotal
                          ></el-pagination>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div style="width:30%">
                    <el-card style="height: 312px;margin-top: 45px">
                      <span style="font-size: 20px; display: flex;position: relative;right: -20px">已选设备</span><br/>
                      <div style="margin-left: 2%;max-height: 60vh;overflow: auto">
                        <el-tag v-for="equipment in formEquipments" style="margin: 2px;" hit="true" closable
                                @close="equipmentRemove(equipment)">
                          {{ equipment.equipmentName }}
                        </el-tag>
                      </div>
                    </el-card>
                  </div>
                </div>
              </div>
              <!--选择仪表-->
              <div v-if="form.type==='仪表点检任务'">
                <div style="display: flex; justify-content:center;">
                  <div style="width: 80%;">
                    <div style="margin-bottom: 21px">
                      <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容"
                                v-model="searchText"></el-input>
                      <el-button type="primary" @click="getMeter(1)">查询</el-button>
                      <el-button type="info" @click="reset2">重置</el-button>
                    </div>
                    <div style="width: 99%">
                      <el-table :data="meterList" border align="center" height="315" stripe
                                :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}">
                        <el-table-column prop="meterNum" label="仪表编号" width="100" align="center"></el-table-column>
                        <el-table-column prop="meterName" label="仪表名称" align="center"></el-table-column>
                        <el-table-column label="选择" align="center" width="80">
                          <template v-slot="scope">
                            <el-button size="mini" type="primary" plain @click="meterAdd(scope.row)">选择</el-button>
                          </template>
                        </el-table-column>
                      </el-table>
                      <el-pagination
                          @current-change="handleCurrentChangeMeter"
                          :current-page="meterPageNum"
                          :page-size="meterPageSize"
                          layout="total, prev, pager, next"
                          :total=meterTotal
                      ></el-pagination>
                    </div>
                  </div>
                  <div style="width:30%">
                    <el-card style="height: 315px;margin-left: 2%; margin-top: 50px; max-height: 60vh;overflow: auto">
                      <span>已选仪表</span><br/>
                      <el-tag v-for="meter in formMeter" style="margin: 2px;" hit="true" closable
                              @close="meterRemove(meter)">
                        {{ meter.meterName }}
                      </el-tag>
                    </el-card>
                  </div>
                </div>
              </div>
            </div>

            <!--选择人员-->
            <div v-if="active === 3">
              <div style="display: flex;justify-content:center;">
                <div style="width: 80%;">
                  <div style="margin-bottom: 10px">
                    <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容"
                              v-model="searchText"></el-input>
                    <el-button type="primary" @click="getPeople(1)">查询</el-button>
                    <el-button type="info" @click="reset1">重置</el-button>
                  </div>
                  <el-table :data="peopleList" border height="315" align="center"  :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}">
                    <el-table-column prop="id" label="用户ID" width="100" align="center"></el-table-column>
                    <el-table-column prop="department" label="部门" width="100" align="center"></el-table-column>
                    <el-table-column prop="name" label="人员姓名" align="center"></el-table-column>
                    <el-table-column label="选择" align="center" width="80">
                      <template v-slot="scope">
                        <el-button size="mini" type="primary" plain @click="peopleAdd(scope.row)">选择</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <el-pagination
                      @current-change="handleCurrentChangePeoPle"
                      :current-page="peoplePageNum"
                      :page-size="peoplePageSize"
                      layout="total, prev, pager, next"
                      :total=peopleTotal
                  ></el-pagination>
                </div>
                <div style="width:30%">
                  <el-card style="height: 315px;margin-left: 2%; margin-top: 45px; max-height: 60vh;overflow: auto">
                    <span>已选人员</span><br/>
                    <el-tag v-for="people in formPeople" style="margin: 2px;" hit="true" closable
                            @close="peopleRemove(people)">
                      {{ people.name }}
                    </el-tag>
                  </el-card>
                </div>
              </div>
            </div>

            <!--创建任务-->
            <div v-if="active==4">
              <div style="width: 50%">
                <h1>任务信息</h1>
                <div style="margin-bottom:14px"><span style="color: #606A75">任务名称:</span>&emsp;<span>{{form.packageName}}</span></div>
                <div style="margin-bottom:14px"><span style="color: #606A75">任务类型:</span>&emsp;<span>{{form.type}}</span></div>
                <div style="margin-bottom:14px" v-if="form.type!=='临时任务'"><span style="color: #606A75">任务周期:</span>&emsp;<span>——{{ form.checkCycle }}h</span></div>
                <div style="margin-bottom:14px"><span style="color: #606A75">点检时间:</span>&emsp;<span>{{form.startTime}}—{{ form.endTime }}</span></div>
                <div id="wrap"><span style="color: #606A75">任务描述:</span>&emsp;<span>{{ form.description }}</span></div>
              </div>
              <div style="width: 100%;display: flex;flex-direction: column">
                <div v-if="form.type!=='临时任务'">
                  <div style="margin-top: 14px; width: 800px;height: 1px;border: 1px solid #E3E7ED;"></div>
                  <div style="width: 50%" v-if="this.form.type==='设备点检任务'">
                    <h1 style="text-align: center">已选设备</h1>
                    <el-tag style="margin: 2px;" hit="true" v-for="text in formEquipments">{{ text.equipmentName }}
                    </el-tag>
                  </div>
                  <div style="width: 50%" v-if="this.form.type==='仪表点检任务'">
                    <h1 style="text-align: center">已选仪表</h1>
                    <el-tag style="margin: 2px;" hit="true" v-for="text in formMeter">{{ text.meterName }}</el-tag>
                  </div>
                </div>
                <div style="margin-top: 14px;width: 800px;height: 1px;border: 1px solid #E3E7ED;"></div>
                <div style="width: 50%">
                  <h1 style="">已选人员</h1>
                  <el-tag style="margin: 2px;" hit="true" v-for="text in formPeople">{{ text.name }}</el-tag>
                </div>
              </div>
            </div>
            <div style="text-align: center;">
              <el-button v-if="active!==0" style="background-color: #54ba9c;border-color:#54ba9c"
                         @click="next" type="success">{{ text }}
              </el-button>
            </div>
          </div>
        </el-card>
      </div>

    </div>
  </div>
</template>
<script>

import {left} from "core-js/internals/array-reduce";
import {formatTime} from "@/utils";

export default {
  name: "TaskCreate",
  data() {
    return {
      searchText: '',
      //默认数据
      active: 0,
      authority: 0,//这是发布给谁的任务，1是运行工，2是维修工，4是安全员
      text: "下一步",
      time: new Date,
      任务类型列表: [
        {value: '设备点检任务', label: '设备点检任务'},
        {value: '仪表点检任务', label: '仪表点检任务'}
      ],
      // 任务周期列表: [
      //   {value: '周期任务', label: '周期任务'},
      //   {value: '临时任务', label: '临时任务'}
      // ],
      //需要提交到数据库的数据
      form: {
        packageId: '',//任务包id
        packageName: '',//任务包名称
        description: '',//任务包描述
        type: '设备点检任务',//任务包类型
        taskCycle: '周期任务',//任务周期
        checkCycle: null,//点检周期
        startTime: '',//开始时间
        endTime: '',//结束时间
        is_publish: false,
      },
      formEquipments: [],
      formMeter: [],
      formPeople: [],
      //需要从数据库获取的数据
      equipmentsList: [],
      equipmentsPageNum: 1,
      equipmentsPageSize: 8,
      equipmentsTotal: '',
      equipmentsDepartment: '',

      meterList: [],
      meterPageNum: 1,
      meterPageSize: 8,
      meterTotal: '',
      meterDepartment: '',

      peopleList: [],
      peoplePageNum: 1,
      peoplePageSize: 8,
      peopleTotal: '',
      peopleDepartment: '',

      rules: {
        packageName: [
          {required: true, message: '请输入任务名称', trigger: 'blur'},
          {min: 1, message: '任务名称不可为空', trigger: 'blur'},
        ],
      },
      pageSize: 10,
      currentPage: 1,
    };
  },
  methods: {
    left,
    reset() {
      this.searchText = '';
      this.getDevice(1);
    },
    reset1() {
      this.searchText = '';
      this.getPeople(1);
    },
    reset2() {
      this.searchText = '';
      this.getMeter(1);
    },
    re0() {
      if (this.active > 0) {
        this.form.type= '设备点检任务';
        this.active = 0;
        this.text = '下一步';
      }
    },
    re1() {
      if (this.active > 1) {
        this.active = 1;
        this.text = '下一步';
      }
    },
    re2() {
      if (this.active > 2) {
        if (this.form.type === '临时任务') return;
        this.active = 2;
        this.getDevice(1);
        this.text = '下一步';
      }
    },
    re3() {
      if (this.active > 3) {
        this.active = 3;
        this.getPeople(1);
        this.text = '下一步';
      }
    },
    next() {
      if (this.active === 0) {
        this.active++;
        return;
      }
      if (this.active === 1) {
        console.log(this.time);
        this.form.startTime = formatTime(this.time[0], 'yyyy-MM-dd HH:mm');
        this.form.endTime = formatTime(this.time[1], 'yyyy-MM-dd HH:mm');
        console.log(this.form.startTime);
        console.log(this.form.endTime);
        switch (this.form.type) {
          case '设备点检任务':
            this.getDevice(1);
            this.active++;
            break;
            case '仪表点检任务':
              this.getMeter(1);
              this.active++;
              break;
              case '临时任务':
                this.formEquipments.length = 0;
                this.getPeople(1);
                this.active += 2;
                break;
        }
        // if (this.form.taskCycle === '周期任务') {
        //   if (this.form.type === '设备点检任务') {
        //     this.getDevice(1);
        //   } else {
        //     this.getMeter(1);
        //   }
        //   this.active++;
        // } else {
        //   this.formEquipments.length = 0;
        //   this.getPeople(1);
        //   this.active += 2;
        // }
        return;
      }
      if (this.active === 2) {
        if (this.formEquipments.length === 0 && this.formMeter.length === 0) {
          this.$message.error('至少选择一个设备')
          return;
        }
        this.getPeople(1);
        this.active++;
        return;
      }
      if (this.active === 3) {
        if (this.formPeople.length === 0) {
          this.$message.error('至少选择一个人员')
          return;
        }
        this.text = "创建";
        this.active++;
        return;
      }
      if (this.active === 4) {
        let taskType;
        if (this.form.type === '设备点检任务') taskType = '3';
        if (this.form.type === '仪表点检任务') taskType = '4';
        if (this.form.type === '临时任务') taskType = '8';
        this.form.packageId = '' + this.authority+ taskType + new Date().getTime();
        let i = 0;
        if (this.form.type === '设备点检任务') {
          while (this.formEquipments[i]) {
            this.formEquipments[i].packageId = this.form.packageId;
            i++;
          }
        } else {
          while (this.formMeter[i]) {
            this.formMeter[i].packageId = this.form.packageId;
            i++;
          }
        }
        i = 0;
        while (this.formPeople[i]) {
          this.formPeople[i].packageId = this.form.packageId;
          i++;
        }
        if (this.form.packageName === '') this.form.packageName = '任务包';
        if (this.form.description === '') this.form.description = '暂无描述';
        console.log(this.form);
        console.log(this.formEquipments);
        console.log(this.formMeter)
        console.log(this.formPeople);
        this.pushForm();
      }
    },
    getDevice(pageNum) {
      if (pageNum) this.equipmentsPageNum = pageNum
      this.$request.get('/deviceList/selectByPage', {
        params: {
          pageNum: this.equipmentsPageNum,
          pageSize: this.equipmentsPageSize,
          equipmentName: '',
          type: '',
          department: this.equipmentsDepartment,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.equipmentsList = res.data.records
        this.equipmentsTotal = res.data.total
      })
    },
    handleCurrentChangeDevice(pageNum) {
      this.getDevice(pageNum)
    },
    equipmentsAdd(row) {
      let exists = this.formEquipments.some(item =>
          item.equipmentNum === row.equipmentNum && item.equipmentName === row.equipmentName
      );
      if (!exists) {
        let json = {
          equipmentNum: row.equipmentNum,
          equipmentName: row.equipmentName
        };
        this.formEquipments.push(json);
        console.log(this.formEquipments);
      } else {
        this.$message.error('已选择' + row.equipmentName);
      }
    },
    equipmentRemove(num) {
      console.log('执行删除' + num);
      this.formEquipments.splice(this.formEquipments.indexOf(num), 1);
    },

    getMeter(pageNum) {
      if (pageNum) this.meterPageNum = pageNum
      if(this.authority === 1){
        this.$request.get('/meterList1/selectByPage', {
          params: {
            pageNum: this.meterPageNum,
            pageSize: this.meterPageSize,
            searchText: this.searchText
          }
        }).then(res => {
          this.meterList = res.data.records
          this.meterTotal = res.data.total
        })
        console.log(this.meterList);
      }
      if(this.authority === 2){
        this.$request.get('/meterList2/selectByPage', {
          params: {
            pageNum: this.meterPageNum,
            pageSize: this.meterPageSize,
            searchText: this.searchText
          }
        }).then(res => {
          this.meterList = res.data.records
          this.meterTotal = res.data.total
        })
        console.log(this.meterList);
      }

    },
    handleCurrentChangeMeter(pageNum) {
      this.getMeter(pageNum);
    },
    meterAdd(row) {
      let exists = this.formMeter.some(item =>
          item.meterNum === row.meterNum && item.meterName === row.meterName
      );
      if (!exists) {
        let json = {
          meterNum: row.meterNum,
          meterName: row.meterName,
          equipmentNum: row.equipmentNum,
          equipmentName: row.equipmentName,
          department: row.department,
          standard: row.standards,
          standardDes: row.standardDes
        };
        this.formMeter.push(json);
        console.log(this.formMeter);
      } else {
        this.$message.error('已选择' + row.meterName);
      }
    },
    meterRemove(num) {
      console.log('执行删除' + num);
      this.formMeter.splice(this.formMeter.indexOf(num), 1);
    },

    getPeople(pageNum) {  // 分页查询
      if (pageNum) this.peoplePageNum = pageNum
      this.$request.get('/user/selectByPage', {
        params: {
          pageNum: this.peoplePageNum,
          pageSize: this.peoplePageSize,
          authority: this.authority === 1 ? "点检员" : this.authority === 2 ? "维修工" : "安全员",
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.peopleList = res.data.records
        this.peopleTotal = res.data.total
      })
    },
    handleCurrentChangePeoPle(pageNum) {
      this.getPeople(pageNum)
    },
    peopleAdd(row) {
      let exists = this.formPeople.some(item =>
          item.account === row.account && item.name === row.name
      );
      if (!exists) {
        let json = {
          account: row.account,
          name: row.name
        };
        this.formPeople.push(json);
        console.log(this.formPeople);
      } else {
        this.$message.error('已选择' + row.name);
      }
    },
    peopleRemove(num) {
      console.log('执行删除' + num);
      this.formPeople.splice(this.formPeople.indexOf(num), 1);
    },
    async pushForm() {
      let requests = [];
      let i = 0;

      if (this.form.type === '设备点检任务') {
        let request = this.$request({
          url: '/taskDevice/add',
          method: 'POST',
          data: this.formEquipments
        }).then(res => {
          if (res.code === '200') {
            return true;
          } else {
            console.log(res);
            return res.code;
          }
        });
        requests.push(request);
      }
      if (this.form.type === '仪表点检任务') {
        let request = this.$request({
          url: '/taskMeter/add',
          method: 'POST',
          data: this.formMeter
        }).then(res => {
          if (res.code === '200') {
            return true;
          } else {
            console.log(res);
            return res.code;
          }
        });
        requests.push(request);
      }

      i = 0;
      while (this.formPeople[i]) {
        let request = this.$request({
          url: '/taskUser/add',
          method: 'POST',
          data: this.formPeople[i]
        }).then(res => {
          if (res.code === '200') {
            return true;
          } else {
            console.log(res);
            return res.code;
          }
        });
        requests.push(request);
        i++;
      }

      // 添加主任务请求
      if(this.form.type === '临时任务')this.form.isPublish=true
      let mainTaskRequest = this.$request({
        url: '/task/add',
        method: 'POST',
        data: this.form
      }).then(res => {
        if (res.code === '200') {
          return true;
        } else {
          console.log(res);
          return res.code;
        }
      });
      console.log(this.form);
      requests.push(mainTaskRequest);

      // 等待所有请求完成
      await Promise.all(requests);

      // 跳转到 TaskView 页面
      await this.$router.push('/TaskView');
    },
  }
}
</script>

